  display: inline-block;
  padding: 1.4rem 1.6rem;
  border: 2px solid #e8e6ed;
  border-radius: 4px;
  margin-right: 0.8rem;
  font-family: "Manrope-Medium";
  font-size: 1.5rem;
  line-height: 2rem;
