  border-radius: 4px;
  cursor: pointer;
  color: #2c2b3c;
  background-color: transparent;
}
.cmn__select::before {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
  content: "";
  width: 2.4rem;
  height: 2.4rem;
  background-image: url("../assets/img/dropdown-arr.png");
  background-repeat: no-repeat;
}
.cmn select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: "";
}
.cmn input[type=radio]:checked + .cmn__label {
  border: 2px solid #841DA4;
}
.cmn__input {
  width: 100%;
  font-family: "Manrope-Medium";
  font-size: 1.5rem;
  line-height: 2.2rem;
  background-color: #f8f8f9;
  backdrop-filter: blur(20px);
  border: 1px solid #B7B3C2;
  border-radius: 4px;
  padding: 1.2rem 1.6rem;
  color: #2c2b3c;
  opacity: 0.5;
